/* used bootstrap icons ---------------------------------------------------- */
/* non-bootstrap variables --------------------------------------- */
/* utilities ----------------------------------------------------- */
.tobago-display-inline-block {
  display: inline-block;
}

/* main ---------------------------------------------------------- */
.tobago-auto-spacing {
  /* Since bootstrap 5.0.0-alpha1 class "form-group" is removed. In the Bootstrap demo "form-group" is replaced with
  "mb-3". But we shouldn't use "mb-3", because it's adding an "!important" to the margin-bottom. With "!important" added
  custom styling is much more complicated. */
  margin-bottom: 1rem;
}

tobago-badge {
  background-color: #d7d7d7;
}
tobago-badge.badge.bg-primary, tobago-badge.badge.btn-primary {
  color: #fff;
}
tobago-badge.badge.bg-secondary, tobago-badge.badge.btn-secondary {
  color: #000;
}
tobago-badge.badge.bg-success, tobago-badge.badge.btn-success {
  color: #000;
}
tobago-badge.badge.bg-info, tobago-badge.badge.btn-info {
  color: #000;
}
tobago-badge.badge.bg-warning, tobago-badge.badge.btn-warning {
  color: #000;
}
tobago-badge.badge.bg-danger, tobago-badge.badge.btn-danger {
  color: #fff;
}
tobago-badge.badge.bg-light, tobago-badge.badge.btn-light {
  color: #000;
}
tobago-badge.badge.bg-dark, tobago-badge.badge.btn-dark {
  color: #fff;
}
tobago-badge.badge.btn {
  border: 0;
}
tobago-badge.btn {
  cursor: initial;
}

/* bar -------------------------------------------------------------- */
tobago-bar.navbar-light .navbar-brand > .tobago-link {
  color: #d90;
}
tobago-bar.navbar-light .navbar-brand > .tobago-link:hover, tobago-bar.navbar-light .navbar-brand > .tobago-link:focus {
  text-decoration: none;
}
tobago-bar.navbar-dark .navbar-brand > .tobago-link {
  color: #fff;
}
tobago-bar.navbar-dark .navbar-brand > .tobago-link:hover, tobago-bar.navbar-dark .navbar-brand > .tobago-link:focus {
  text-decoration: none;
}
tobago-bar .navbar-brand {
  /* Normally links are underlined. A navbar-brand link is NOT underlined. In Tobago, the .navbar-brand class is not
  on the link itself but on the parent tag. */
}
tobago-bar .navbar-brand > .tobago-link {
  text-decoration: none;
}

tobago-behavior {
  display: none;
}

/* box -------------------------------------------------------------- */
tobago-box > .card-body {
  overflow-x: hidden;
  overflow-y: auto;
}

.tobago-box-header {
  display: flex;
  justify-content: space-between;
  align-content: center;
}

.card-header > h3:first-child, .card-header > .h3:first-child {
  margin-bottom: 0;
  align-self: center;
}
.card-header .navbar {
  padding: 0;
}

/* button, link ------------------------------------------------------- */
.tobago-button:disabled {
  pointer-events: auto;
}
.tobago-button:disabled:hover {
  cursor: not-allowed;
}
.tobago-button > img + span {
  margin-left: 0.4em;
}
.tobago-button > i + span {
  margin-left: 0.4em;
}
.tobago-button.badge.bg-primary, .tobago-button.badge.btn-primary {
  color: #fff;
}
.tobago-button.badge.bg-secondary, .tobago-button.badge.btn-secondary {
  color: #000;
}
.tobago-button.badge.bg-success, .tobago-button.badge.btn-success {
  color: #000;
}
.tobago-button.badge.bg-info, .tobago-button.badge.btn-info {
  color: #000;
}
.tobago-button.badge.bg-warning, .tobago-button.badge.btn-warning {
  color: #000;
}
.tobago-button.badge.bg-danger, .tobago-button.badge.btn-danger {
  color: #fff;
}
.tobago-button.badge.bg-light, .tobago-button.badge.btn-light {
  color: #000;
}
.tobago-button.badge.bg-dark, .tobago-button.badge.btn-dark {
  color: #fff;
}
.tobago-button.badge.btn {
  border: 0;
}

/* button -------------------------------------------------------------- */
/* XXX for what is this needed?
a.tobago-button {
  &.btn-primary:focus {
    color: $btn-primary-color;
  }
  &.btn-secondary:focus {
    color: $btn-secondary-color;
  }
}
*/
button.tobago-button:disabled {
  cursor: not-allowed;
}
button.tobago-button:disabled img, button.tobago-button:disabled span {
  pointer-events: none;
}

tobago-buttons > .tobago-button {
  display: inline-block;
}

/* collapsible -------------------------------------------------------------- */
tobago-box.tobago-collapsed .card-body,
tobago-section.tobago-collapsed .tobago-section-content,
tobago-panel.tobago-collapsed {
  display: none;
}

/* date -------------------------------------------------------------- */
tobago-date {
  display: block;
}
tobago-date input {
  min-width: 7em;
}
tobago-date input:disabled {
  color: rgba(33, 37, 41, 0.5);
}
tobago-date input.border-danger:focus {
  box-shadow: 0 0 0 0.25rem rgba(211, 0, 64, 0.25);
}
tobago-date input.border-warning:focus {
  box-shadow: 0 0 0 0.25rem rgba(240, 173, 78, 0.25);
}
tobago-date input.border-info:focus {
  box-shadow: 0 0 0 0.25rem rgba(91, 192, 222, 0.25);
}
tobago-date input::-ms-clear {
  /* Remove IE10's "clear field" X button */
  display: none;
}

/* dropdown ------------------------------------------------------- */
tobago-dropdown {
  display: inline-block;
}
tobago-dropdown .dropdown-menu .form-check {
  /* move form-check components to the right, so checkbox/radio buttons are inside dropdown-menu */
  padding-left: 2.5em;
}
